// material
import {
  Box,
  Grid,
  Container,
  Typography,
  Stack,
  Button,
} from "@material-ui/core";
import arrowUp from "@iconify/icons-ant-design/arrow-up-outline";
import arrowDown from "@iconify/icons-ant-design/arrow-down-outline";
import { styled } from "@material-ui/core/styles";
import { useQuery, UseQueryResult } from "react-query";
// components

import {
  DonationsGrowth,
  RecentTransactions,
  MyWalletIncomeCard,
  YourBalanceCard,
  AddCard,
} from "./components"; 
import useAxios from "../../../../../utils/useAxios";
import { WalletApiResponse } from "../../../../../utils/typeHelpers";
import Loader from "../../../../../components/Loader";
import ApiErrorPage from "../../../../Errror/ApiErrorPage";
import { useNavigate } from "react-router";
// ----------------------------------------------------------------------

const CustomButton = styled(Button)(() => ({
  backgroundColor: "rgba(22, 182, 152, 0.65)",
  boxShadow: "0px 2px 2px rgba(0, 0, 0, 0.15)",
  borderRadius: 22,

  height: 45,
  fontsize: 20,
  fontWeight: 600,
  color: "#F8F9FA",
}));

export default function Wallet() {
  let axiosInstance = useAxios();
  const navigate = useNavigate();
  const {
    data,
    isLoading,
    isError,
    error,
  }: UseQueryResult<WalletApiResponse, Error> = useQuery(
    ["walletDashboard"],
    async () => {
      const response = await axiosInstance.get(`/dashboard/my-wallet/`);

      return response.data;
    }
  );

  // console.log("wallet data:", data?.recent_transaction);
  

  return (
    <div>
      {isLoading ? (
        <Loader />
      ) : isError ? (
        error && <ApiErrorPage error={error} />
      ) : (
        data && (
          <Container maxWidth="xl">
            <Stack
              direction="row"
              justifyContent="space-between"
              alignItems="center"
              spacing={2}
            >
              <Grid item xs={12} sm={6} md={4}>
                <Typography sx={{ fontSize: 32, fontWeight: "bold" }}>
                  My Wallet
                </Typography>
              </Grid>
              <Grid item xs={12} sm={6} md={4} />
              <Grid item xs={12} sm={6} md={4}>
                <CustomButton
                  onClick={() => navigate("../fundtransfer")}
                  fullWidth
                  variant="contained"
                >
                  Transfer Funds
                </CustomButton>
              </Grid>
            </Stack>

            <Grid container spacing={3} pt={3}>
              <Grid item xs={12} sm={6} md={4}>
                <MyWalletIncomeCard
                  label="Total Income"
                  labelColor="#16B698"
                  value={`$${data?.total_income}`}
                  icon={arrowUp}
                  pecentage={`${data?.today_income_percentage}`}
                  iconColor={"#16B698"}
                />
              </Grid>
              <Grid item xs={12} sm={6} md={4}>
                <MyWalletIncomeCard
                  label="Total Outcome"
                  labelColor="#FB7339"
                  value="$7,010"
                  icon={arrowDown}
                  pecentage="1.5"
                  iconColor={"#CF1010"}
                />
              </Grid>
              <Grid item xs={12} sm={6} md={4}>
                <YourBalanceCard
                  balance={`${data?.total_income}`}
                  balanceToday={`${data?.today_income}`}
                  icon={arrowUp}
                  iconColor="#16B698"
                />
              </Grid>

              <Grid item xs={12} sm={8}>
                <DonationsGrowth monthly_income={data.monthly_income} />
              </Grid>
              <Grid item xs={12} sm={4}>
                <RecentTransactions recent_transaction={data?.recent_transaction} />
              </Grid>
              <Grid item xs={12} sm={8}></Grid>
              <Grid item xs={12} sm={4}>
                <AddCard />
              </Grid>
            </Grid>
          </Container>
        )
      )}
    </div>
  );
}
